	module.exports = {"data":{"markdownRemark":{"html":"<p>Sometimes it’s useful to see a log of all the actions and <code>atom</code> state changes. <strong>Tiny Atom</strong> ships with a built in console logger.</p>\n<p>Enable it like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">const</span> createAtom <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'tiny-atom'</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">const</span> log <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'tiny-atom/log'</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">const</span> atom <span class=\"token operator\">=</span> <span class=\"token function\">createAtom</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> evolve<span class=\"token punctuation\">,</span> render<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> debug<span class=\"token punctuation\">:</span> log <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>Now open browser’s console in your app and you should see what actions are firing and what state updates are applied.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 652px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.74846625766872%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"Console Logger\"\n        title=\"\"\n        src=\"/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-0cf7c.png\"\n        srcset=\"/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-f4966.png 197w,\n/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-1a4f2.png 393w,\n/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-0cf7c.png 786w,\n/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-7651b.png 1179w,\n/tiny-atom/static/console-logger-92aafd5aa895576cf2cf2f0d1f647779-e6149.png 1304w\"\n        sizes=\"(max-width: 652px) 100vw, 652px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Legend:</p>\n<ul>\n<li>rows containing <span style='font-weight: bold; color: #05823d'>green</span> action names are actions</li>\n<li>rows containing only <span style='font-weight: bold; color: #888'>gray</span> action names are updates</li>\n<li>two dots <code>••</code> indicate a top level action</li>\n<li>one dot <code>•</code> indicate a nested action</li>\n<li>the object at the end of the row is the action payload for action rows</li>\n<li>the object at the end of the row is the patch object for update rows</li>\n<li>expanded rows contain more details about each row</li>\n</ul>","excerpt":"Sometimes it’s useful to see a log of all the actions and  atom  state changes.  Tiny Atom  ships with a built in console logger. Enable it…","timeToRead":1,"frontmatter":{"title":"Console logger"},"parent":{"__typename":"File","relativePath":"console-logger.md"}}},"pathContext":{"slug":"/console-logger/"}}

